* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* navBar component ********************************************************************************************************* */
.navBarParent header {
  height: 90px;
  background-color: #f7f7f7;
  box-shadow: 0px 2px 45px rgba(0, 0, 0, 0.13);

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px;
  z-index: 1;
}

.navBarParent nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding: 0 50px;
}

.navBarParent .logo {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
  text-decoration: none;
  color: #1f271b;
}

.navBarParent .logo .NavLink {
  text-decoration: none;
  color: #1f271b;
  font-family: "Amatic SC", cursive;
  font-size: 50px;
  font-weight: 700;
}

.navBarParent .nav-links {
  display: flex;
  justify-content: space-between;
  list-style: none;
  margin: 0;
  padding: 0;
  align-items: center;
}

.navBarParent .nav-links li {
  color: #333;
  font-size: 20px;
  font-weight: bold;
  text-decoration: none;
  margin-left: 30px;
  color: black;
  font-family: "Nunito Sans", sans-serif;
  text-decoration: none;
}

.navBarParent .nav-links li p {
  color: black;
}

.navBarParent .nav-links li p span {
  background-color: #499eee;
  padding: 10px 15px;
  color: white;
  border-radius: 5px;
}

.navBarParent .nav-links li a {
  text-decoration: none;
}

.navBarParent .nav-links li p:hover {
  color: #499eee;
}

.navBarParent .nav-links li:first-child p {
  margin-left: 0;
}

.navBarParent .cart {
  font-size: 50px;
  transition: all 0.3s ease;
}

/* CSS for screens larger than 768px */
.navBarParent .hamburger {
  display: none;
}

.navBarParent .notification {
  background-color: #ff4242;
  color: white;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  font-size: 12px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -5px;
  right: -10px;
  padding: 10px;
}

.navBarParent .cart {
  position: relative;
}

.navBarParent .cart .notification {
  position: absolute;
}

/* CSS for screens smaller than 768px */
@media (max-width: 1000px) {
  .navBarParent .nav-links {
    display: none;
  }

  .navBarParent nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    padding: 0 40px;
  }

  .navBarParent .nav-links li {
    margin-left: 0;
  }

  .navBarParent .logo h1 {
    font-size: 45px;
  }

  .navBarParent .hamburger {
    display: flex;
    align-items: center;
    cursor: pointer;
  }

  .navBarParent .hamburger svg {
    width: 30px;
    height: 30px;
    fill: #fff;
  }

  .navBarParent .nav-links .show {
    transition: all 0.3s ease-in-out;
  }

  .navBarParent .nav-links {
    position: fixed;
    top: 90px;
    right: -50%;
    width: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: all 0.3s ease-in-out;
    z-index: 0;
    background-color: white;
  }

  .navBarParent .nav-links.show {
    right: 0;
  }

  .navBarParent .nav-links.hide {
    right: -50%;
  }

  .navBarParent .nav-links li p {
    margin: 15px 0;
    color: black;
  }

  .navBarParent .nav-links li p {
    font-size: 17px;
  }

  .navBarParent .nav-links li:first-child {
    margin-top: 0;
  }

  .navBarParent .nav-links li:last-child {
    margin-bottom: 0;
  }
}

@media (max-width: 576px) {
  .navBarParent .logo h1 {
    color: #1f271b;
    font-size: 40px;
  }
}

@media (max-width: 576px) {
  .navBarParent nav {
    padding: 0 20px;
  }

  .navBarParent .nav-links {
    width: 30%;
  }

  .navBarParent .logo h1 {
    color: #1f271b;
    font-size: 35px;
  }

  .navBarParent .nav-links li p {
    font-size: 15px;
  }
}

@media (max-width: 540px) {
  .navBarParent .nav-links {
    width: 40%;
  }
}
@media (max-width: 400px) {
  .navBarParent .nav-links {
    width: 50%;
  }
}

/* hero component ************************************************************************************************************ */
