/* footer component ****************************************************************************************************************************** */

.footerParent {
  background-color: #333333;
  padding: 30px;
  font-size: 16px;
  color: white;
  margin-top: 100px;
}

.footerParent footer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.footerParent .footer-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 100px;
  width: 80%;
}

.footerParent .footer-column {
  margin-bottom: 20px;
}

.footerParent .footer-column h4 {
  font-size: 20px;
  margin-bottom: 10px;
}

.footerParent .footer-column p {
  margin-bottom: 10px;
}

.footerParent .social-media-icons {
  list-style: none;
  padding: 0;
  display: flex;
  margin: 0;
}

.footerParent .social-media-icons svg {
  fill: white;
  height: 30px;
  width: 30px;
  margin-right: 10px;
  transition: all 0.2s ease-in-out;
}

.footerParent .social-media-icons svg:hover {
  fill: #01baef;
  transform: scale(1.2);
  cursor: pointer;
}

.footerParent hr {
  border-top: 0.5 solid #ddd;
  margin: 30px 0;
  width: 80%;
}

.footerParent p {
  margin: 0;
}

.footerParent input[type="email"] {
  padding: 10px;
  width: 100%;
  margin-bottom: 10px;
  border: 1px solid #ccc;
}

.footerParent button[type="submit"] {
  background-color: red;
  color: #fff;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
}

.footerParent button[type="submit"]:hover {
  background-color: #646464;
}

@media (max-width: 1200px) {
  .footerParent .footer-container {
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 50px;
  }
}

@media (max-width: 900px) {
  .footerParent .footer-container {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;
  }
}

@media (max-width: 600px) {
  .footerParent .footer-container {
    grid-template-columns: 1fr;
    grid-gap: 20px;
  }
}

