/* category component ******************************************************************************************************************* */
.categoryParent {
  width: 100%;
  min-height: 400px;
  margin: 0 auto;
  padding: 0 20px;
  background-color: #f7f7f7;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 130px;
}

.categoryParent .top-category-section {
  flex-direction: column;
  text-align: center;
  margin-top: 40px;
  width: 75%;
}

.categoryParent .top-category-section h2 {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 50px;
  font-family: "Amatic SC", cursive;
  font-size: 5rem;
}

.categoryParent .category-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.categoryParent .category-card {
  margin-bottom: 40px;
  padding: 20px;
  box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  text-align: center;
  width: 30%;
  background-color: white;
  transition: ease-in-out 0.1s;
  min-width: 30%;
  text-decoration: none;
  cursor: pointer;
  color: #333333;
}

.categoryParent .category-card:hover {
  cursor: pointer;
  transform: translateY(-15px);
  box-shadow: 0px 8px 30px rgba(0, 0, 0, 0.3);
}

.categoryParent .category-card:active {
  box-shadow: 0px 8px 30px rgba(0, 0, 0, 0.3);
  transform: translateY(-5px);
}

.categoryParent .category-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 5px;
  margin-bottom: 20px;
}

.categoryParent .category-card h3 {
  font-size: 1.35rem;
  font-weight: bold;
  margin-bottom: 10px;
}

.categoryParent .category-card p {
  font-size: 0.9rem;
  color: #777;
  line-height: 1.4;
  margin-top: 10px;
}

@media (max-width: 768px) {
  .categoryParent .top-category-section {
    width: 90%;
  }

  .categoryParent .category-card {
    width: 45%;
    min-width: 45%;
  }
}

@media (max-width: 480px) {
  .categoryParent .top-category-section {
    width: 100%;
  }

  .categoryParent .category-card {
    width: 100%;
    min-width: 100%;
  }

  .categoryParent .top-category-section h2 {
    font-size: 3.5rem;
  }
}

/* loading screen **************************************************************************************************** */
.loader {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 10px;
  width: 40px;
  height: 40px;
  position: relative;
  border: 4px solid white;
  border-top: 4px solid #499eee;
  border-radius: 50%;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.loading-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

/* contact us page *************************************************************************************************************** */

.contactParent {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 9%;
}

.contactParent .contact-page {
  display: flex;
  gap: 15px;
  width: 70%;
  background-color: #fff;
  padding: 30px;
  box-shadow: 0px 4px 40px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
}

.contactParent .map-container {
  width: 50%;
  height: 450px;
  margin-right: 20px;
  border-radius: 10px;
}

.contactParent .map-container iframe {
  width: 100%;
  height: 100%;
  border-radius: 10px;
}

.contactParent .contact-form {
  width: 50%;
}

.contactParent .contact-form form {
  justify-content: space-between;
}

.contactParent .contact-form textarea {
  height: 130px;
  resize: none;
}

.contactParent .contact-form h2 {
  font-size: 1.8rem;
  margin-bottom: 5px;
}

.contactParent .contact-form hr {
  margin-bottom: 15px;
}

.contactParent .form-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.contactParent .form-group {
  flex-basis: 48%;
}

.contactParent label {
  display: flex;
  align-items: center;
  font-size: 1rem;
  margin-bottom: 8px;
}

.contactParent label i {
  display: flex;
  align-items: center;
  margin-right: 5px;
}

.contactParent input,
.contactParent textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.contactParent button[type="submit"] {
  padding: 10px 20px;
  background-color: #499eee;
  color: #fff;
  border: none;
  font-size: 1rem;
  cursor: pointer;
  margin-top: 20px;
}

.contactParent button[type="submit"]:hover {
  transform: scale(1.05);
  transition: background-color 0.3s, transform 0.3s, box-shadow 0.3s;
}

.contactParent button[type="submit"]:active {
  transform: scale(1);
  transition: background-color 0.3s, transform 0.3s, box-shadow 0.3s;
}

