/* shopping section ************************************************************************************************************************* */
.shoppingParent {
  font-family: Arial, sans-serif;
  margin-top: 150px;
  display: flex;
  align-items: center;
  height: auto;
  flex-direction: column;
}

.navBarParent .filterIcon {
  display: none;
}

.shoppingParent .container {
  display: flex;
  flex-wrap: wrap;
  width: 70%;
  margin: auto;
  justify-content: space-between;
  height: 100%;
}

.shoppingParent .left-section {
  width: 18%;
  background-color: #eef0f2;
  padding: 20px;
  height: auto;
  border-radius: 5px;
