}

.shoppingParent .price-range input[type="range"] {
  width: 100%;
  height: 8px;
  background-color: #ddd;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
  border: none;
}

.shoppingParent .price-range p {
  margin: 10px 0;
  font-size: 14px;
