/* pagination ****************************************************************************** */
.pagination {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}

.pagination .page-list {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pagination .pageButton,
.pagination .page-item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  margin: 0 5px;
  border: 1px solid #ccc;
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.pagination .pageButton:hover,
.pagination .page-item:hover {
  background-color: #eaeaea;
}

.pagination .paginationButtonDisabled {
  pointer-events: none;
  opacity: 0.5;
}

.pagination .active {
  background-color: #2196f3;
  color: #fff;
}

.pagination .GrFormPreviousLink,
.pagination .GrFormNextLink {
  font-size: 18px;
}

/* Customize the icons */
.pagination .GrFormPreviousLink::before,
.pagination .GrFormNextLink::before {
  display: inline-block;
  font-family: "Material Icons";
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  font-feature-settings: "liga";
  -webkit-font-feature-settings: "liga";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.pagination .GrFormPreviousLink::before {
  content: "\\E5C4"; /* Icon for previous link */
}

.pagination .GrFormNextLink::before {
  content: "\\E5C8"; /* Icon for next link */
}
